.profile__name {
  font-size: 42px;
  line-height: 48px;
  font-weight: 500;
  margin: 0;
  max-width: 450px;
}

@media (max-width: 910px) {
  .profile__name {
    font-size: 27px;
    line-height: 33px;
  }
}

@media (max-width: 720px) {
  .profile__name {
    max-width: 60vw;
  }
}